import Card from "../card/Card";

const Post = ({ src, data, titulo, children }) => {
    return (
        <section id="post__content" className="col-12">
            <Card>
                <img width="300" src={ src } alt={ titulo } />
                <section className="post__descricao">    
                    <br/>
                    <h6>Data: { data }</h6>                    
                    <h1>{ titulo }</h1>
                    <br/>
                    { children }
                    <br/>
                </section>
            </Card>
        </section>
    )
}

export default Post;